import Vue from 'vue'
import App from './App.vue'
import VueResource from 'vue-resource';
import VueRouter from 'vue-router';
import { routes } from './routes';
import './directives/Transform';
import VeeValidate from 'vee-validate';
import msg from './pt_BR';
import 'bootstrap/dist/css/bootstrap.css';
import './assets/css/teste.css';
import './assets/js/teste.js';

import 'bootstrap/dist/js/bootstrap.js';

Vue.use(VueResource);
Vue.http.options.root = process.env.API_URL ? process.env.API_URL : 'http://localhost:3000';

// Vue.http.interceptors.push((req, next) => {

//     //  possvel colocar informaes no header antes do envio da requisio
//     req.headers.set('Authorization', 'informao de segurana aqui');
//     console.log('Lidando com o request');

//     next(res => {
//         console.log('Lidando com a resposta')
//             //  possvel acessar os dados da reposta e realizar transformaes antes
//         console.log(res.body);
//     });
// });

Vue.use(VueRouter);
Vue.use(VeeValidate, {
    locale: 'pt_BR',
    dictionary: {
        pt_BR: {
            messages: msg
        }
    }
});

const router = new VueRouter({
    routes,
    mode: 'history'
});

new Vue({
    el: '#app',
    router,
    render: h => h(App)
})



